import { CanvasWrapper } from '..';
export class Canvas {
    canvasWrapper: CanvasWrapper;
    hostDiv: HTMLElement;

    canvas: HTMLCanvasElement;
    ctx: CanvasRenderingContext2D;

    constructor(canvasWrapper: CanvasWrapper, hostDiv: HTMLElement) {
        this.canvasWrapper = canvasWrapper;
        this.hostDiv = hostDiv;
        this.InitCanvas(this.hostDiv);
    }
    private InitCanvas(hostDiv: HTMLElement) {
        this.canvas = document.createElement('canvas');
        this.canvas.style.position = 'absolute';
        this.canvas.style.zIndex = '0';
        this.hostDiv = hostDiv;
        this.hostDiv.appendChild(this.canvas);
        this.canvas.id = 'CanvasRenderer';
        this.canvas.className = 'njslab-web-core-canvas-renderer';
        this.canvas.style.pointerEvents = 'none';
        this.ctx = this.canvas.getContext('2d') as CanvasRenderingContext2D;

        this.canvas.width = this.hostDiv.clientWidth; // hostDivForSize.clientWidth;
        this.canvas.height = this.hostDiv.clientHeight; // hostDivForSize.clientHeight;
    }
    ResizeCanvas(width: number, height: number) {
        this.canvas.width = width;
        this.canvas.height = height;
    }
}